@Composable
fun NavigationTopBar(
    uiState: UiState,
    navController: NavHostController,
    scaffoldState: ScaffoldState,
    scope: CoroutineScope
) {
    val navBackStackEntry by navController.currentBackStackEntryAsState()
    val currentRoute = navBackStackEntry?.destination?.route
    TopAppBar(
        title = {
            //Text(text = currentRoute.orEmpty(), color = Color.White)
            Text(text = "GSB-VyaparBhagya", color = Color.White)
        },
        navigationIcon = {
            IconButton(onClick = { scope.launch { scaffoldState.drawerState.open() } }) {
                Icon(imageVector = Icons.Filled.Menu, contentDescription = "Drawer",)
            }
        },
        actions = {
            IconButton(onClick = { scope.launch { onAccountClick() } }) {
                if (isUserLoggedIn()) {
                    uiState.getImage(uiState.getUser().getImage().ifEmpty { BLANK_PROFILE.value() })?.let { imageBitmap ->
                        Icon(bitmap = imageBitmap, contentDescription = "Account", tint = Color.Unspecified, modifier = Modifier.width(35.dp).height(35.dp).clip(CircleShape))
                    }?: PlaceHolder(35.dp, 35.dp)
                } else {
                    Icon(imageVector = Icons.Filled.AccountCircle, contentDescription = "Account",)
                }
            }
        },
        contentColor = Color.White,
        elevation = 12.dp,
        backgroundColor = primary,
    )
}