package com.gsb.vyapar.bhagya.presentation.common.image

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.ImageBitmap
import androidx.compose.ui.graphics.painter.BitmapPainter
import androidx.compose.ui.graphics.painter.Painter
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.gsb.vyapar.bhagya.theme.placeHolder
import com.gsb.vyapar.bhagya.theme.splash
import org.jetbrains.compose.resources.painterResource

@Composable
fun CommonImage(painter: Painter, modifier: Modifier = Modifier) {
    Image(painter, contentDescription = "GVB", modifier = modifier)
}

@Composable
fun CommonImageRound(painter: Painter, modifier: Modifier = Modifier) {
    Image(painter, contentDescription = "GVB", modifier = modifier.clip(CircleShape))
}

@Composable
fun SplashImage() {
    CommonImage(painterResource(splash))
}

@Composable
fun SplashImageCircular() {
    CommonImageRound(painterResource(splash))
}

@Composable
fun CommonImage(imageBitmap: ImageBitmap, width: Dp = 100.dp, height: Dp = 100.dp, modifier: Modifier = Modifier) {
    Image(
        painter = BitmapPainter(imageBitmap),
        contentDescription = "image",
        modifier = modifier.width(width).height(height),
    )
}

@Composable
fun CommonImageRound(imageBitmap: ImageBitmap, width: Dp = 100.dp, height: Dp = 100.dp, modifier: Modifier = Modifier) {
    Image(
        painter = BitmapPainter(imageBitmap),
        contentDescription = "image",
        modifier = modifier.width(width).height(height).clip(CircleShape),
    )
}

@Composable
fun PlaceHolder(width: Dp = 100.dp, height: Dp = 100.dp) {
    Image(
        painter = painterResource(placeHolder),
        contentDescription = "placeHolder",
        modifier = Modifier.width(width).height(height),
    )
}
