package com.gsb.vyapar.bhagya.domain

import kotlinx.serialization.Serializable

@Serializable
data class Location(
    val address: String?,
    val city: String?,
    val district: String?,
    val latitude: Double?,
    val longitude: Double?,
    val state: String?
) {
    fun getAddressString(): String {
        return "$address, $city, $district, $state"
    }
}
