@OptIn(ExperimentalFoundationApi::class)
@Composable
fun AdminCategoriesView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        var selectedCategory by remember { mutableStateOf(Categories.D.Category()) }
        Column(Modifier.fillMaxSize()) {
            Toolbar("AdminCategoriesView")
            Row(modifier = Modifier.fillMaxWidth().weight(8.0f)) {
                LazyColumn(
                    modifier = Modifier.width(130.dp),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    items(uiState.getCategories().getCategoriesList()) { category ->
                        Card(
                            modifier = Modifier.width(120.dp).height(120.dp),
                            shape = RoundedCornerShape(8.dp),
                            backgroundColor = if (selectedCategory.id == category.id) unfocussed else offwhite,
                        ) {
                            Column(
                                horizontalAlignment = Alignment.CenterHorizontally,
                                verticalArrangement = Arrangement.Center,
                                modifier = Modifier.combinedClickable(
                                    onClick = { selectedCategory = category },
                                    onLongClick = { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.AddEditCategory(category))) })
                            ) {
                                uiState.getImage(category.imageUrl)?.let { imageBitmap -> CommonImage(imageBitmap, 50.dp, 50.dp) }
                                Text14RegularB(text = category.name, color = black)
                            }
                        }
                        Spacer(Modifier.height(8.dp))
                    }
                    item {
                        Spacer(Modifier.height(8.dp))
                        uiState.getImage(ADD_URL.value())?.let { imageBitmap ->
                            CommonImage(imageBitmap, 50.dp, 50.dp, Modifier.clickable { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.AddEditCategory(null))) })
                        }
                        Spacer(Modifier.height(8.dp))
                    }
                }
                LazyColumn(modifier = Modifier.fillMaxWidth().padding(8.dp)) {
                    items(selectedCategory.getSubCategoriesList(uiState.getSubCategories())) { subCategory ->
                        Card(
                            modifier = Modifier.fillMaxWidth().height(50.dp).clickable { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.AddEditSubCategory(subCategory))) },
                            shape = RoundedCornerShape(8.dp),
                            backgroundColor = offwhite,
                        ) {
                            Box(contentAlignment = Alignment.Center) {
                                Text14RegularB(text = subCategory.name, color = black)
                            }
                        }
                        Spacer(Modifier.height(8.dp))
                    }
                    item {
                        Spacer(Modifier.height(8.dp))
                        GVBButton(text = "Add SubCategory", onClick = { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.AddEditSubCategory(null))) })
                        Spacer(Modifier.height(8.dp))
                    }
                }
            }
            Column(Modifier.weight(2.0f), verticalArrangement = Arrangement.Center) {
                GVBButton(
                    text = "Save Categories",
                    onClick = { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.SaveCategories)) })
                Spacer(Modifier.height(16.dp))
                GVBButton(
                    text = "Save SubCategories",
                    onClick = { onTriggerEvent(UiEvents.CategoryEvents(CategoriesViewUiEvents.SaveSubCategories)) })
            }
        }
    }
}
