package com.gsb.vyapar.bhagya.datasource.cache

import app.cash.sqldelight.async.coroutines.awaitAsList
import app.cash.sqldelight.async.coroutines.awaitAsOne
import app.cash.sqldelight.async.coroutines.awaitAsOneOrNull
import com.gsb.vyapar.bhagya.domain.Categories
import com.gsb.vyapar.bhagya.domain.SubCategories
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.launchDefault
import com.gsb.vyapar.bhagya.util.DatabaseDriverFactory
import kotlinx.datetime.Clock

object SqlDelightClient {
    private var database: GsbVyaparBhagyaDatabase? = null
    private var queries: GsbVyaparBhagyaDatabaseQueries? = null
    private val maxCacheSize: Int = 1000

    fun initDatabase() {
        launchDefault {
            database = GsbVyaparBhagyaDatabase(DatabaseDriverFactory().createDriver())
        }
    }

    private fun getQueries(): GsbVyaparBhagyaDatabaseQueries? {
        if (queries == null) queries = database?.gsbVyaparBhagyaDatabaseQueries
        return queries
    }

    suspend fun insertOrUpdateCategory(category: Categories.D.Category) {
        if (category.id > 0) {
            getQueries()?.updateCategory(category.id, category.name, category.imageUrl, category.deleted)
        } else {
            getQueries()?.insertCategory(category.name, category.imageUrl, category.deleted)
        }
    }

    suspend fun updateCategories(categories: Categories) {
        categories.d.categories.forEach {
            insertOrUpdateCategory(it)
        }
    }

    suspend fun getCategories(): List<CategoryEntity> {
        return getQueries()?.getCategories()?.awaitAsList().orEmpty()
    }

    suspend fun getSubCategories(): List<SubCategoryEntity> {
        return getQueries()?.getSubCategories()?.awaitAsList().orEmpty()
    }

    suspend fun insertOrUpdateSubCategory(subCategory: SubCategories.D.SubCategory) {
        if (subCategory.id > 0) {
            getQueries()?.updateSubCategory(subCategory.id, subCategory.name, subCategory.categoryId, subCategory.deleted)
        } else {
            getQueries()?.insertSubCategory(subCategory.name, subCategory.categoryId, subCategory.deleted)
        }
    }

    suspend fun updateSubCategories(subCategories: SubCategories) {
        subCategories.d.subCategories.forEach {
            insertOrUpdateSubCategory(it)
        }
    }

    suspend fun getSubCategoriesOfCategory(categoryId: Long): List<SubCategoryEntity> {
        return getQueries()?.getSubCategoriesOfCategory(categoryId)?.awaitAsList().orEmpty()
    }

    suspend fun putImage(url: String, data: ByteArray) {
        val currentTime = Clock.System.now().epochSeconds
        getQueries()?.updateImageCache(url, data, currentTime)
        val totalItems = getQueries()?.countImagesCache()?.awaitAsOne() ?: 0
        if (totalItems > maxCacheSize) {
            val itemsToRemove = totalItems - maxCacheSize
            getQueries()?.evictOldest(itemsToRemove)
        }
    }

    suspend fun getImage(url: String): ByteArray? {
        val currentTime = Clock.System.now().epochSeconds
        val image = getQueries()?.getImage(url)?.awaitAsOneOrNull()
        if (image != null) {
            getQueries()?.updateAccessTime(currentTime, url)
        }
        return image
    }
}