import com.gsb.vyapar.bhagya.theme.primary
import com.gsb.vyapar.bhagya.theme.splash
import com.gsb.vyapar.bhagya.util.*
import org.jetbrains.compose.resources.painterResource

@Composable
fun AboutView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val uriHandler = LocalUriHandler.current
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Column(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar("About GSB-VyaparBhagya")
            Column(
                modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth()
                    .padding(16.dp),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Spacer(Modifier.height(15.dp))
                CommonImage(painterResource(splash), Modifier.width(200.dp).height(200.dp))
                Spacer(Modifier.height(15.dp))
                Text14RegularB(getAbout(), black)
                Spacer(Modifier.height(15.dp))
                Text14RegularBB(getEmail(), Color.Blue, Modifier.clickable { uriHandler.openUri("mailto:${getEmail()}") })
                Spacer(Modifier.height(15.dp))
                Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Start) {
                    Text14RegularB("Contact Us:", black)
                }
                Spacer(Modifier.height(8.dp))
                Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.SpaceEvenly) {
                    uiState.getImage(FB.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri(getFb()) }) }?: PlaceHolder(35.dp, 35.dp)
                    uiState.getImage(XTWITTER.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri(getX()) }) }?: PlaceHolder(35.dp, 35.dp)
                    uiState.getImage(INSTA.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri(getInst()) }) }?: PlaceHolder(35.dp, 35.dp)
                    uiState.getImage(MAIL.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri("mailto:${getEmail()}") }) }?: PlaceHolder(35.dp, 35.dp)
                    uiState.getImage(WHATSAPP.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri("https://wa.me/+91${getMobile()}") }) }?: PlaceHolder(35.dp, 35.dp)
                    uiState.getImage(WEBSITEIMG.value())?.let { imageBitmap -> CommonImage(imageBitmap, 35.dp, 35.dp, Modifier.clickable { uriHandler.openUri(getWebsite()) }) }?: PlaceHolder(35.dp, 35.dp)
                }
                Spacer(Modifier.height(8.dp))
                Text14RegularB("App is available in PlayStore, AppStore & Web:", black)
                Spacer(Modifier.height(8.dp))
                Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Center) {
                    uiState.getImage(PLAYSTORE.value())?.let { imageBitmap -> CommonImage(imageBitmap, 85.dp, 60.dp, Modifier.clickable { uriHandler.openUri(getAndroid()) }) }?: PlaceHolder(150.dp, 60.dp)
                    Spacer(Modifier.width(10.dp))
                    uiState.getImage(APPSTORE.value())?.let { imageBitmap -> CommonImage(imageBitmap, 85.dp, 60.dp, Modifier.clickable { uriHandler.openUri(getIos()) }) }?: PlaceHolder(150.dp, 60.dp)
                    Spacer(Modifier.width(10.dp))
                    uiState.getImage(WEBAPP.value())?.let { imageBitmap -> CommonImage(imageBitmap, 85.dp, 60.dp, Modifier.clickable { uriHandler.openUri(getWebApp()) }) }?: PlaceHolder(150.dp, 60.dp)
                }
                Spacer(Modifier.height(8.dp))
                Text14RegularB(getSupport(), black)
                Spacer(Modifier.height(16.dp))
                uiState.getImage(getUpi())?.let { imageBitmap -> CommonImage(imageBitmap, 150.dp, 150.dp) }
                Spacer(Modifier.height(16.dp))
                Row(modifier = Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Center) {
                    GVBButtonSmall(text = "Support Us") { uriHandler.openUri(uiState.getInfo().getWebsite()) }
                    Spacer(Modifier.width(40.dp))
                    GVBButtonSmall(text = "Share") { share(getShare()) }
                }
                Spacer(Modifier.weight(1f))
                Row(modifier = Modifier.fillMaxWidth().padding(top = 16.dp), horizontalArrangement = Arrangement.SpaceBetween) {
                    Text14RegularB("Privacy Policy", primary, Modifier.clickable { uriHandler.openUri(getPrivacyUrl()) })
                    Text14RegularB("Terms & Conditions", primary, Modifier.clickable { uriHandler.openUri(getTermsUrl()) })
                }
            }
        }
    }
}