package com.gsb.vyapar.bhagya.presentation.business.uistate

import com.gsb.vyapar.bhagya.domain.Business
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.getState
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.updateState

data class BusinessUiState(
    val title: String = "Manage Business",
    val mBusinessList: List<Business> = listOf(),
    val isRefreshing: Boolean = false,
    val addEditBusiness: Business = Business(),
) {
    fun updateMBusinessList(mBusinessList: List<Business>) {
        updateAuthUiState(this.copy(mBusinessList = mBusinessList))
    }

    fun resetMBusinessList() {
        updateAuthUiState(this.copy(mBusinessList = listOf()))
    }

    fun updateIsRefreshing(isRefreshing: Boolean) {
        updateAuthUiState(this.copy(isRefreshing = isRefreshing))
    }

    fun updateAddEditBusiness(addEditBusiness: Business) {
        updateAuthUiState(this.copy(addEditBusiness = addEditBusiness))
    }
}

fun updateAuthUiState(businessUiState: BusinessUiState) {
    updateState(getState().copy(businessUiState = businessUiState))
}

fun getBusinessUiState(): BusinessUiState {
    return getState().businessUiState
}
