@Composable
fun getEditTextColor(): TextFieldColors {
    return TextFieldDefaults.outlinedTextFieldColors(
        backgroundColor = white,
        textColor = Black,
        cursorColor = Black,
        focusedBorderColor = focussed,
        unfocusedBorderColor = focussed,
        unfocusedLabelColor = unfocussed,
        focusedLabelColor = message
    )
}

@Composable
fun EmailEditText(
    text: String,
    enabled: Boolean = true,
    label: String = "Enter Email",
    onTextChange: (String) -> Unit,
) {
    val keyboardController = LocalSoftwareKeyboardController.current

    Column {
        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            shape = RoundedCornerShape(8.dp),
            value = text,
            singleLine = true,
            textStyle = TextStyle(
                fontFamily = interMedium(),
                fontSize = 14.sp,
                fontWeight = FontWeight(500)
            ),
            onValueChange = { onTextChange(it) },
            label = { Text14Medium(label, hintColor) },
            keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Email),
            keyboardActions = KeyboardActions(onDone = { keyboardController?.hide() }),
            colors = getEditTextColor(),
            isError = isEmailError(text),
            enabled = enabled,
        )
        if (isEmailError(text)) {
            Spacer(Modifier.height(4.dp))
            Row {
                Spacer(Modifier.width(16.dp))
                Text12Regular(
                    text = "Enter valid Email",
                    color = MaterialTheme.colors.error,
                )
            }
        }
    }
}

@Composable
fun PasswordEditText(
    text: String,
    hint: String = "",
    onTextChange: (String) -> Unit,
) {
    val keyboardController = LocalSoftwareKeyboardController.current
    var passwordVisible by rememberSaveable { mutableStateOf(false) }
    Column {
        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            shape = RoundedCornerShape(8.dp),
            value = text,
            singleLine = true,
            textStyle = TextStyle(
                fontFamily = interMedium(),
                fontSize = 14.sp,
                fontWeight = FontWeight(500)
            ),
            onValueChange = { onTextChange(it) },
            label = { Text14Medium(hint, hintColor) },
            keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Password),
            keyboardActions = KeyboardActions(onDone = { keyboardController?.hide() }),
            colors = getEditTextColor(),
            visualTransformation = if (passwordVisible) VisualTransformation.None else PasswordVisualTransformation(),
            trailingIcon = {
                val painter = painterResource(if (passwordVisible) visibility_on else visibility_off)
                IconButton(onClick = { passwordVisible = !passwordVisible }) {
                    Icon(painter = painter, "password")
                }
            },
            isError = isPasswordError(text)
        )
        if (isPasswordError(text)) {
            Spacer(Modifier.height(4.dp))
            Row {
                Spacer(Modifier.width(16.dp))
                Text12Regular(
                    text = "Enter valid password",
                    color = MaterialTheme.colors.error,
                )
            }
        }
    }
}

