data class SectionData(val user: User, val businesses: List<Business>)

@Composable
fun ExpandableList(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit, sections: List<SectionData>) {
    val isExpandedMap = rememberSavableSnapshotStateMap {
        List(sections.size) { index: Int -> index to false }
            .toMutableStateMap()
    }
    LazyColumn(
        content = {
            sections.onEachIndexed { index, sectionData ->
                Section(
                    uiState,
                    onTriggerEvent,
                    sectionData = sectionData,
                    isExpanded = isExpandedMap[index] ?: false,
                    onHeaderClick = {
                        isExpandedMap[index] = !(isExpandedMap[index] ?: false)
                    }
                )
            }
        }
    )
}

@OptIn(ExperimentalMaterialApi::class)
@Composable
fun SectionHeader(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit,sectionData: SectionData, isExpanded: Boolean, onHeaderClicked: () -> Unit) {
    Card(
        modifier = Modifier.fillMaxWidth(),
        shape = RoundedCornerShape(8.dp),
        backgroundColor = offwhite,
        onClick = { onHeaderClicked() }
    ) {
        Row(modifier = Modifier
            .clickable { onHeaderClicked() }
            .padding(vertical = 8.dp, horizontal = 16.dp)) {
            Row(modifier = Modifier.weight(1.0f)) {
                uiState.getImage(sectionData.user.getImage().ifEmpty { BLANK_PROFILE.value() })?.let { imageBitmap -> CommonImageRound(imageBitmap, 80.dp, 80.dp) }?: PlaceHolder(80.dp, 80.dp)
                Spacer(Modifier.width(15.dp))
                Column(
                    modifier = Modifier.fillMaxWidth(),
                    verticalArrangement = Arrangement.Center
                ) {
                    Text14RegularBB("${sectionData.user.getFirstName()} ${sectionData.user.getLastName()}", black)
                    Text14RegularBB(sectionData.user.getMobile(), black)
                    Text14RegularBB(sectionData.user.getEmail(), black)
                }
            }
            if (sectionData.businesses.isNotEmpty()) {
                if (isExpanded) {
                    CommonImage(painterResource(arrow_up))
                } else {
                    CommonImage(painterResource(arrow_down))
                }
            }
        }
    }
}

fun LazyListScope.Section(
    uiState: UiState,
    onTriggerEvent: (UiEvents) -> Unit,
    sectionData: SectionData,
    isExpanded: Boolean,
    onHeaderClick: () -> Unit
) {
    item {
        Column(Modifier.fillMaxWidth().padding(8.dp)) {
            SectionHeader(
                uiState,
                onTriggerEvent,
                sectionData = sectionData,
                isExpanded = isExpanded,
                onHeaderClicked = onHeaderClick
            )
        }
    }
    if (isExpanded) {
        items(sectionData.businesses) {
            Column(Modifier.fillMaxWidth().padding(horizontal = 8.dp)) {
                BusinessCard(uiState, onTriggerEvent, it) {
                    launchAddEditMBusiness(it)
                }
            }
        }
    }
}

@Composable
fun <K, V> rememberSavableSnapshotStateMap(init: () -> SnapshotStateMap<K, V>): SnapshotStateMap<K, V> =
    rememberSaveable(saver = snapshotStateMapSaver(), init = init)

fun <K, V> snapshotStateMapSaver() = Saver<SnapshotStateMap<K, V>, Any>(
    save = { state -> state.toList() },
    restore = { value ->
        @Suppress("UNCHECKED_CAST")
        (value as? List<Pair<K, V>>)?.toMutableStateMap() ?: mutableStateMapOf<K, V>()
    }
)