val gMapsRegex = """^https:\/\/maps\.app\.goo\.gl\/[A-Za-z0-9]+$"""
val gmapsFullRegex = """^https:\/\/(www\.)?google\.com\/maps\/(place|dir|search|@).*"""
fun isValidGMap(gmap: String): Boolean {
    return gmap.matches(gMapsRegex.toRegex()) || gmap.matches(gmapsFullRegex.toRegex())
}

fun isGMapError(text: String): Boolean {
    return text.isNotEmpty() && !isValidGMap(text)
}
