package com.gsb.vyapar.bhagya.datasource.network.response

import kotlinx.serialization.Serializable

@Serializable
data class ImageUploadResponse(
    val data: Data?,
    val message: String?,
    val status: Int?,
    val success: Boolean?
) {
    @Serializable
    data class Data(
        val createdAt: String?,
        val extension: String?,
        val size: Int?,
        val title: String?,
        val url: String?,
        val viewUrl: String?
    )
}