@Composable
fun AdminView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Column(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar("Admin View")
            Column(Modifier.verticalScroll(rememberScrollState()).padding(horizontal = 16.dp)) {
                Spacer(Modifier.height(16.dp))
                GVBButton(text = "AddCategory") { pushView(Screens.AddCategory) }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = "AdminCategories") { pushView(Screens.AdminCategories) }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = "Info") { pushView(Screens.InfoScreen) }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = "Config") { pushView(Screens.ConfigScreen) }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = "Review Business") { pushView(Screens.ReviewBusiness) }
                Spacer(Modifier.height(16.dp))
            }
        }
    }
}