package com.gsb.vyapar.bhagya.presentation.splash.util

import com.gsb.vyapar.bhagya.datasource.cache.SqlDelightClient
import com.gsb.vyapar.bhagya.datasource.network.KtorClient
import com.gsb.vyapar.bhagya.datasource.preference.Preference
import com.gsb.vyapar.bhagya.datasource.runtimecache.RuntimeCache
import com.gsb.vyapar.bhagya.domain.Config
import com.gsb.vyapar.bhagya.domain.*
import com.gsb.vyapar.bhagya.domain.Info
import com.gsb.vyapar.bhagya.domain.SubCategories
import com.gsb.vyapar.bhagya.domain.decodeCategoriesString
import com.gsb.vyapar.bhagya.domain.decodeConfigString
import com.gsb.vyapar.bhagya.domain.decodeSubCategoriesString
import com.gsb.vyapar.bhagya.domain.encodeCategoriesString
import com.gsb.vyapar.bhagya.domain.encodeConfigString
import com.gsb.vyapar.bhagya.domain.encodeInfoString
import com.gsb.vyapar.bhagya.domain.encodeSubCategoriesString
import com.gsb.vyapar.bhagya.domain.toCategories
import com.gsb.vyapar.bhagya.domain.toConfig
import com.gsb.vyapar.bhagya.domain.toInfo
import com.gsb.vyapar.bhagya.domain.toSubCategories
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.getState
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.updateState
import com.gsb.vyapar.bhagya.interactor.util.showError
import com.gsb.vyapar.bhagya.presentation.Screens.*
import com.gsb.vyapar.bhagya.util.getMessage

fun goToNavigation() {
    updateState(getState().copy(mainView = Navigation))
}

fun getLastBusinessNumber(): Int {
    return getState().config.decodeConfigString().getBusinessNumber()
}

suspend fun checkToken() {
    if (RuntimeCache.accessToken.isEmpty()) {
        val result = KtorClient.anonymousLogin()
        if (result.isSuccess) {
            val authResponse = result.getOrNull()
            RuntimeCache.setTokens(authResponse?.idToken.orEmpty(), authResponse?.refreshToken.orEmpty())
        }
    }
}

suspend fun getConfig() {
    try {
        val config = getState().config.decodeConfigString()
        val version = KtorClient.getConfigVersion()
        if (config.v < (version.getOrNull() ?: 0)) {
            getConfigAPI()
        }
    } catch (e: Exception) {
        getConfigAPI()
    }
}

private suspend fun getConfigAPI() {
    val result = KtorClient.getConfig()
    if (result.isSuccess) {
        updateState(getState().copy(config = result.getOrNull()?.toConfig()?.encodeConfigString() ?: Config().encodeConfigString()))
        Preference.config = getState().config
    } else {
        showError("Fetch Config failure : ${result.exceptionOrNull().getMessage()}")
    }
}

suspend fun getCategories() {
    try {
        val config = getState().config.decodeConfigString()
        val categories = getState().categories.decodeCategoriesString()
        if (categories.v < (config.d.config[CATEGORY] ?: 0)) {
            getCategoriesAPI()
        }
    } catch (e: Exception) {
        getCategoriesAPI()
    }
}

private suspend fun getCategoriesAPI() {
    val result = KtorClient.getCategories()
    if (result.isSuccess) {
        updateState(getState().copy(categories = result.getOrNull()?.toCategories()?.encodeCategoriesString() ?: com.gsb.vyapar.bhagya.domain.Categories().encodeCategoriesString()))
        Preference.categories = getState().categories
        SqlDelightClient.updateCategories(getState().categories.decodeCategoriesString())
    } else {
        showError("Fetch Categories failure : ${result.exceptionOrNull().getMessage()}")
    }
}

suspend fun getSubCategories() {
