package com.gsb.vyapar.bhagya.presentation.navigation.util

import com.gsb.vyapar.bhagya.datasource.network.KtorClient
import com.gsb.vyapar.bhagya.datasource.preference.Preference
import com.gsb.vyapar.bhagya.domain.*
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.getState
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.launchDefault
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.updateState
import com.gsb.vyapar.bhagya.interactor.util.*
import com.gsb.vyapar.bhagya.presentation.Screens
import com.gsb.vyapar.bhagya.presentation.auth.util.*

fun pushView(screen: Screens) {
    val navStack = getState().navViewStack
    navStack.push(screen)
    updateState(getState().copy(navView = navStack.peek(), navViewStack = navStack))
}

fun popView() {
    val navStack = getState().navViewStack
    navStack.pop()
    updateState(getState().copy(navView = navStack.peek(), navViewStack = navStack))
}

fun onAccountClick() {
    try {
        val user = getState().getUser()
        if (!isUserLoggedIn()) {
            launchLoginMobile()
        } else {
            checkUser(user.getId())
        }
    } catch (e: Exception) {
        launchLoginMobile()
    }
}

fun checkUser(userId: String) {
    showLoading()
    launchDefault {
        val result = KtorClient.getUser(userId)
        hideLoading()
        if (result.isSuccess) {
            updateState(getState().copy(user = result.getOrNull()?.toUser()?.encodeUserString() ?: User().encodeUserString()))
            Preference.user = getState().user
            checkUserAfterLogin()
        } else {
            showError("Failed to load retry")
        }
    }
}

fun checkUserAfterLogin() {
    val user = getState().getUser()
    if (user.getEmail().isEmpty()) {
        launchVerifyEmail()
    } else  {
        pushView(Screens.Register)
    }
}