        ) {
            Icon(
                imageVector = Icons.AutoMirrored.Filled.ArrowBack,
                contentDescription = "Back",
                tint = white,
                modifier = Modifier.clickable { popView() }
            )
        }
        Text16Medium(text = title, color = white)
