import kotlinx.coroutines.launch

@Composable
fun CategoryView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        var selectedCategory by remember { mutableStateOf(uiState.categoriesViewUiState.category) }
        val listState = rememberLazyListState()
        val coroutineScope = rememberCoroutineScope()
        Column {
            Toolbar(selectedCategory.name)
            Row(modifier = Modifier.fillMaxWidth()) {
                LazyColumn(
                    modifier = Modifier.width(130.dp),
                    horizontalAlignment = Alignment.CenterHorizontally,
                    state = listState
                ) {
                    items(uiState.getCategories().getCategoriesList()) { category ->
                        Card(
                            modifier = Modifier.width(120.dp).height(120.dp),
                            shape = RoundedCornerShape(8.dp),
                            backgroundColor = if (selectedCategory.id == category.id) unfocussed else offwhite,
                        ) {
                            Column(
                                horizontalAlignment = Alignment.CenterHorizontally,
                                verticalArrangement = Arrangement.Center,
                                modifier = Modifier.clickable { selectedCategory = category }
                            ) {
                                uiState.getImage(category.imageUrl)?.let { imageBitmap -> CommonImage(imageBitmap, 50.dp, 50.dp) }?: PlaceHolder(50.dp, 50.dp)
                                Text14RegularB(text = category.name, color = black)
                            }
                        }
                        Spacer(Modifier.height(8.dp))
                    }
                }
                LazyColumn(modifier = Modifier.fillMaxWidth().padding(8.dp)) {
                    item {
                        OutlinedExpandedSelectMenu("Select the SubCategory", selectedCategory.getSubCategoriesList(uiState.getSubCategories()).map { it.name }) {}
                    }
                }
            }
        }
        LaunchedEffect(Unit) {
            coroutineScope.launch { listState.scrollToItem(index = uiState.getCategories().getCategoriesList().indexOf(uiState.getCategories().getCategoriesList().find { it.id == selectedCategory.id })) }
        }
    }
}