@OptIn(ExperimentalMaterialApi::class)
@Composable
fun OutlinedExpandedSelectMenu(
    label: String,
    options: List<String>,
    onSelectionChanged: (List<Int>) -> Unit
) {
    var expanded by remember { mutableStateOf(false) }
    var selectedIndices by remember { mutableStateOf(listOf<Int>()) }

    fun selectAll() {
        selectedIndices = options.indices.toList()
        onSelectionChanged(selectedIndices)
    }

    fun clear() {
        selectedIndices = listOf()
        onSelectionChanged(selectedIndices)
    }

    LaunchedEffect(options) {
        clear()
    }

    ExposedDropdownMenuBox(
        modifier = Modifier.fillMaxWidth(),
        expanded = expanded,
        onExpandedChange = { expanded = !expanded }
    ) {
        OutlinedTextField(
            modifier = Modifier.fillMaxWidth(),
            readOnly = true,
            value = options.filterIndexed { index, _ -> selectedIndices.contains(index) }.joinToString(", "),
            onValueChange = {},
            label = { Text14Regular(text = label, color = message) },
            textStyle = TextStyle(fontFamily = interMedium(), fontSize = 14.sp, fontWeight = FontWeight(500)),
            trailingIcon = { ExposedDropdownMenuDefaults.TrailingIcon(expanded = expanded) },
            colors = getEditTextColor()
        )

        ExposedDropdownMenu(
            modifier = Modifier.fillMaxWidth(),
            expanded = expanded,
            onDismissRequest = { expanded = false }
        ) {
            Row(
                modifier = Modifier.fillMaxWidth().padding(8.dp),
                verticalAlignment = CenterVertically
            ) {
                Row(
                    modifier = Modifier.clickable { selectAll() },
                    verticalAlignment = CenterVertically
                ) {
                    Spacer(modifier = Modifier.width(8.dp))
                    Checkbox(
                        checked = selectedIndices.size == options.size && options.isNotEmpty(),
                        onCheckedChange = { if (selectedIndices.size == options.size) clear() else selectAll() },
                        colors = CheckboxDefaults.colors(checkedColor = green)
                    )
                    Spacer(modifier = Modifier.width(8.dp))
                    Text14RegularB(text = "Select All", color = black)
                }
                Spacer(modifier = Modifier.weight(1f))
                Text14Regular(
                    text = "Clear",
                    color = black,
                    modifier = Modifier.clickable { clear() }
                )
            }
            options.forEachIndexed { index, option ->
                DropdownMenuItem(
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        val newSelection = if (selectedIndices.contains(index)) selectedIndices - index else selectedIndices + index
                        selectedIndices = newSelection
                        onSelectionChanged(selectedIndices)
                    }
                ) {
                    Row(verticalAlignment = CenterVertically) {
                        Checkbox(
                            checked = selectedIndices.contains(index),
                            onCheckedChange = {
                                val newSelection = if (selectedIndices.contains(index)) selectedIndices - index else selectedIndices + index
                                selectedIndices = newSelection
                                onSelectionChanged(selectedIndices)
                            },
                            colors = CheckboxDefaults.colors(checkedColor = green)
                        )
                        Spacer(modifier = Modifier.width(8.dp))
                        Text14Regular(text = option, color = black)
                    }
                }
            }
        }
    }
}

