import com.gsb.vyapar.bhagya.presentation.common.menu.OutlinedExpandedSelectMenu
import com.gsb.vyapar.bhagya.presentation.common.text.Text14Regular
import com.gsb.vyapar.bhagya.presentation.common.text.Text14RegularBB
import com.gsb.vyapar.bhagya.presentation.common.toolbar.Toolbar
import com.gsb.vyapar.bhagya.theme.black
import com.gsb.vyapar.bhagya.util.ADD_URL
import com.gsb.vyapar.bhagya.util.orEmpty
import com.gsb.vyapar.bhagya.util.value
import io.github.vinceglb.filekit.compose.rememberFilePickerLauncher
import io.github.vinceglb.filekit.core.PickerType
import kotlinx.coroutines.launch

@Composable
fun AddEditBusinessView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val uriHandler = LocalUriHandler.current
    var logo by remember { mutableStateOf("") }
    var name by remember { mutableStateOf("") }
    var category by remember { mutableStateOf(Category()) }
    var subCategories = listOf<SubCategories.D.SubCategory>()
    var description by remember { mutableStateOf("") }
    var services by remember { mutableStateOf("") }
    var address by remember { mutableStateOf("") }
    var pincode by remember { mutableStateOf("") }
    var latitude by remember { mutableStateOf("") }
    var longitude by remember { mutableStateOf("") }
    var image1 by remember { mutableStateOf("") }
    var image2 by remember { mutableStateOf("") }
    var image3 by remember { mutableStateOf("") }
    var image4 by remember { mutableStateOf("") }
    var image5 by remember { mutableStateOf("") }
    var email by remember { mutableStateOf("") }
    var phone1 by remember { mutableStateOf("") }
    var phone2 by remember { mutableStateOf("") }
    var website by remember { mutableStateOf("") }
    var gmap by remember { mutableStateOf("") }
    var fb by remember { mutableStateOf("") }
    var insta by remember { mutableStateOf("") }
    var x by remember { mutableStateOf("") }
    var linkedIn by remember { mutableStateOf("") }
    var youtube by remember { mutableStateOf("") }
    var selectedIndex by remember { mutableStateOf(-1) }
    var selectedIndices by remember { mutableStateOf(listOf<Int>()) }
    val coroutineScope = rememberCoroutineScope()
    var isAdd by remember { mutableStateOf(name.isEmpty()) }
    val imagePickerLauncher = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { logo = it } }
    }
    val imagePickerLauncher1 = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image1 = it } }
    }
    val imagePickerLauncher2 = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image2 = it } }
    }
    val imagePickerLauncher3 = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image3 = it } }
    }
    val imagePickerLauncher4 = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image4 = it } }
    }
    val imagePickerLauncher5 = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image5 = it } }
    }
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Column(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar(if (isAdd) "Add Business" else "Edit Business")
            Column(
                modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth()
                    .padding(16.dp),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Spacer(Modifier.height(8.dp))
                uiState.getImage(logo.ifEmpty { ADD_URL.value() })?.let { imageBitmap ->
                    CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher.launch() })
                }?: PlaceHolder()
                Spacer(Modifier.height(4.dp))
                Text14Regular("Select Business Logo / Picture *", black)
                Spacer(Modifier.height(8.dp))
                BusinessNameEditText(name) { name = it }
                Spacer(Modifier.height(8.dp))
                OutlinedExpandedMenu("Select the Category *", uiState.getCategories().getCategoriesList().map { it.name }, selectedIndex) { selected, index ->
                    selectedIndex = index
                    category = uiState.getCategories().getCategoriesList().find { it.name ==  selected} ?: Category()
                }
                Spacer(Modifier.height(8.dp))
                OutlinedExpandedSelectMenu("Select the SubCategory *", category.getSubCategoriesList(uiState.getSubCategories()).map { it.name }, selectedIndices) { indices ->
                    selectedIndices = indices
                    val masterList = category.getSubCategoriesList(uiState.getSubCategories())
                    subCategories = indices.mapNotNull { masterList.getOrNull(it) }
                }
                Spacer(Modifier.height(8.dp))
                DetailEditText("Enter the business description *", description) { description = it }
                Spacer(Modifier.height(8.dp))
                DetailEditText("Enter the business services", services) { services = it }
                Spacer(Modifier.height(8.dp))
                PincodeEditText(pincode) {
                    pincode = it
                    getLocation(pincode) { location ->
                        address = location.getAddressString()
                        latitude = location.latitude.toString()
                        longitude = location.longitude.toString()
                    }
                }
                Spacer(Modifier.height(8.dp))
                AddressEditText(address) { address = it }
                Spacer(Modifier.height(8.dp))
                Text14RegularBB("Click here to find the Latitude & Longitude *", Color.Blue, Modifier.clickable { uriHandler.openUri("https://www.latlong.net/") })
                Spacer(Modifier.height(8.dp))
                LatitudeEditText(latitude) { latitude = it }
                Spacer(Modifier.height(8.dp))
                LongitudeEditText(longitude) { longitude = it }
                Spacer(Modifier.height(8.dp))
                LazyRow(
                    modifier = Modifier.fillMaxWidth().padding(4.dp),
                    horizontalArrangement = Arrangement.spacedBy(12.dp)
                ) {
                    item {
                        Column(horizontalAlignment = Alignment.CenterHorizontally) {
                            uiState.getImage(image1.ifEmpty { ADD_URL.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher1.launch() }) }?: PlaceHolder()
                            Text14Regular("Select Image 1", black)
                        }
                    }
                    item {
                        Column(horizontalAlignment = Alignment.CenterHorizontally) {
                            uiState.getImage(image2.ifEmpty { ADD_URL.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher2.launch() }) }?: PlaceHolder()
                            Text14Regular("Select Image 2", black)
                        }
                    }
                    item {
                        Column(horizontalAlignment = Alignment.CenterHorizontally) {
                            uiState.getImage(image3.ifEmpty { ADD_URL.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher3.launch() }) }?: PlaceHolder()
                            Text14Regular("Select Image 3", black)
                        }
                    }
                    item {
                        Column(horizontalAlignment = Alignment.CenterHorizontally) {
                            uiState.getImage(image4.ifEmpty { ADD_URL.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher4.launch() }) }?: PlaceHolder()
                            Text14Regular("Select Image 4", black)
                        }
                    }
                    item {
                        Column(horizontalAlignment = Alignment.CenterHorizontally) {
                            uiState.getImage(image5.ifEmpty { ADD_URL.value() })?.let { imageBitmap -> CommonImage(imageBitmap, 100.dp, 100.dp, Modifier.clickable { imagePickerLauncher5.launch() }) }?: PlaceHolder()
                            Text14Regular("Select Image 5", black)
                        }
                    }
                }
                Spacer(Modifier.height(8.dp))
                EmailEditText(email, label = "Enter Email *") { email = it }
                Spacer(Modifier.height(8.dp))
                MobileNumberEditText(phone1, label = "Enter Phone number 1 *") { phone1 = it }
                Spacer(Modifier.height(8.dp))
                MobileNumberEditText(phone2, label = "Enter Phone number 2") { phone2 = it }
                Spacer(Modifier.height(8.dp))
                WebsiteEditText("Enter business website", website) { website = it }
                Spacer(Modifier.height(8.dp))
                GMapEditText(gmap) { gmap = it }
                Spacer(Modifier.height(8.dp))
                FbEditText(fb) { fb = it }
                Spacer(Modifier.height(8.dp))
                InstaEditText(insta) { insta = it }
                Spacer(Modifier.height(8.dp))
                XEditText(x) { x = it }
                Spacer(Modifier.height(8.dp))
                LinkedInEditText(linkedIn) { linkedIn = it }
                Spacer(Modifier.height(8.dp))
                YoutubeEditText(youtube) { youtube = it }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = if (isAdd) "Add Business" else "Save Business") {
                    val masterList = category.getSubCategoriesList(uiState.getSubCategories())
                    subCategories = selectedIndices.mapNotNull { masterList.getOrNull(it) }
                    addBusinessUI(false, logo, name, category, subCategories, description, services, address, pincode, latitude, longitude, image1, image2, image3, image4, image5, email, phone1, phone2, website, gmap, fb, insta, x, linkedIn, youtube)
                }
                if (isUserAdmin()) {
                    Spacer(Modifier.height(16.dp))
                    GVBButton(text = "Save Admin") {
                        val masterList = category.getSubCategoriesList(uiState.getSubCategories())
                        subCategories = selectedIndices.mapNotNull { masterList.getOrNull(it) }
                        addBusinessUI(true, logo, name, category, subCategories, description, services, address, pincode, latitude, longitude, image1, image2, image3, image4, image5, email, phone1, phone2, website, gmap, fb, insta, x, linkedIn, youtube)
                    }
                }
                Spacer(Modifier.height(16.dp))
            }
        }
    }
    LaunchedEffect(Unit) {
        val business = uiState.businessUiState.addEditBusiness
        logo = business.getLogo()
        name = business.getName()
        category = uiState.getCategories().getCategoriesList().find { it.id.toString() == business.getCategoryId() } ?: Category()
        selectedIndex = uiState.getCategories().getCategoriesList().map { it.name }.indexOf(category.name)
        val restoredList = business.getSubCategoryIds().split(", ").mapNotNull { it.toIntOrNull() }
        subCategories = category.getSubCategoriesList(uiState.getSubCategories()).filter { restoredList.contains(it.id.toInt()) }
        val masterList = category.getSubCategoriesList(uiState.getSubCategories())
        val subcategoryIds = subCategories.map { it.id }.toSet()
        selectedIndices = masterList.mapIndexedNotNull { index, item -> if (item.id in subcategoryIds) index else null }
        description = business.getDescription()
        services = business.getServices()
        address = business.getAddress()
        pincode = business.getPincode()
        latitude = business.getLatitude()
        longitude = business.getLongitude()
        image1 = business.getImage1()
        image2 = business.getImage2()
        image3 = business.getImage3()
        image4 = business.getImage4()
        image5 = business.getImage5()
        email = business.getEmail()
        phone1 = business.getPhone1()
        phone2 = business.getPhone2()
        website = business.getWebsite()
        gmap = business.getMap()
        fb = business.getFacebook()
        insta = business.getInstagram()
        x = business.getX()
        linkedIn = business.getLinkedIn()
        youtube = business.getYoutube()
        isAdd = name.isEmpty()
    }
}