  private inner class GetSubCategoriesOfCategoryQuery<out T : Any>(
    public val categoryId: Long?,
    mapper: (SqlCursor) -> T,
  ) : Query<T>(mapper) {
    override fun addListener(listener: Query.Listener) {
      driver.addListener("subCategoryEntity", listener = listener)
    }

    override fun removeListener(listener: Query.Listener) {
      driver.removeListener("subCategoryEntity", listener = listener)
    }

    override fun <R> execute(mapper: (SqlCursor) -> QueryResult<R>): QueryResult<R> =
        driver.executeQuery(1_565_364_452, """
    |SELECT subCategoryEntity.id, subCategoryEntity.name, subCategoryEntity.categoryId, subCategoryEntity.deleted
    |FROM subCategoryEntity
    |WHERE deleted = 0
    |AND
    |categoryId = IFNULL(?,categoryId)
    """.trimMargin(), mapper, 1) {
      bindLong(0, categoryId)
    }

    override fun toString(): String = "GsbVyaparBhagyaDatabase.sq:getSubCategoriesOfCategory"
  }

