package com.gsb.vyapar.bhagya

import androidx.compose.ui.graphics.ImageBitmap
import androidx.compose.ui.graphics.toComposeImageBitmap
import com.gsb.vyapar.bhagya.interactor.util.showSuccess
import kotlinx.browser.window
import org.jetbrains.skia.Image
import kotlin.js.Promise
import kotlin.js.json

actual fun ByteArray.toComposeImageBitmap(): ImageBitmap {
    return Image.makeFromEncoded(this).toComposeImageBitmap()
}

actual fun share(text: String) {
    val shareData = json(
        "title" to "Share",
        "text" to text,
        "url" to window.location.href
    )
    if (js("navigator.share !== undefined")) {
        val promise: Promise<Unit> = js("navigator.share").unsafeCast<(dynamic) -> Promise<Unit>>()(shareData)
        promise.catch { console.log("Sharing failed: $it") }
    } else {
        window.navigator.clipboard.writeText(text)
        showSuccess("Text copied to clipboard. Share it manually!")
    }
}