@Composable
fun NavigationDrawerView(
    uiState: UiState,
    onTriggerEvent: (UiEvents) -> Unit,
    navController: NavHostController,
    scaffoldState: ScaffoldState,
    scope: CoroutineScope
) {
    val uriHandler = LocalUriHandler.current
    Column(
        modifier = Modifier.fillMaxHeight().width(400.dp).background(color = white),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Spacer(Modifier.height(25.dp))
        CommonImage(painterResource(splash), Modifier.width(200.dp).height(200.dp))
        //uiState.getImage(LOGOIMG.value())?.let { imageBitmap -> CommonImage(imageBitmap, 200.dp, 200.dp) }?: PlaceHolder(200.dp, 200.dp)
        Spacer(Modifier.height(15.dp))
        Text24SemiBold("GSB-VyaparBhagya", black)
        Spacer(Modifier.height(15.dp))
        Column(
            modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth().padding(16.dp),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            val user = uiState.getUser()
            if (!isUserLoggedIn()) {
                GVBButton(text = "Login") {
                    launchLoginMobile()
                    scope.launch { scaffoldState.drawerState.close() }
                }
                Spacer(Modifier.height(15.dp))
//                GVBButton(text = "Admin") {
//                    scope.launch { scaffoldState.drawerState.close() }
//                    pushView(Screens.AdminView)
//                }
//                Spacer(Modifier.height(15.dp))
            } else {
                Row(
                    modifier = Modifier.height(110.dp).fillMaxWidth().background(offwhite).clickable {
                            checkUserAfterLogin()
                            scope.launch { scaffoldState.drawerState.close() }
                        }.padding(16.dp), verticalAlignment = Alignment.CenterVertically
                ) {
                    uiState.getImage(user.getImage().ifEmpty { BLANK_PROFILE.value() })?.let { imageBitmap -> CommonImageRound(imageBitmap, 80.dp, 80.dp) }?: PlaceHolder(80.dp, 80.dp)
                    Spacer(Modifier.width(15.dp))
                    Column(
                        modifier = Modifier.fillMaxWidth(),
                        verticalArrangement = Arrangement.Center
                    ) {
                        Text14RegularBB("${user.getFirstName()} ${user.getLastName()}", black)
                        Text14RegularBB(user.getMobile(), black)
                        Text14RegularBB(user.getEmail(), black)
                    }
                }
                Spacer(Modifier.height(15.dp))
                GVBButton(text = "Manage Business") {
                    scope.launch { scaffoldState.drawerState.close() }
                    pushView(Screens.ManageBusiness)
                }
                if (isUserAdmin()) {
                    Spacer(Modifier.height(15.dp))
                    GVBButton(text = "Admin") {
                        scope.launch { scaffoldState.drawerState.close() }
                        pushView(Screens.AdminView)
                    }
                }
                Spacer(Modifier.height(15.dp))
                GVBButton(text = "Sign Out") {
                    logout()
                }
            }
            Spacer(Modifier.height(15.dp))
            Row(
                modifier = Modifier.height(40.dp).fillMaxWidth()
                    .clickable { uriHandler.openUri(uiState.getInfo().getWebsite()) },
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Icon(
                    imageVector = Icons.Filled.ThumbUp,
                    contentDescription = "ThumbUp",
                    tint = black
                )
                Spacer(Modifier.width(8.dp))
                Text14RegularB("Support Us", black)
            }
            Spacer(Modifier.height(15.dp))
            Row(
                modifier = Modifier.height(40.dp).fillMaxWidth()
                    .clickable { share(getShare()) },
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Icon(imageVector = Icons.Filled.Share, contentDescription = "Share", tint = black)
                Spacer(Modifier.width(8.dp))
                Text14RegularB("Share", black)
            }
            Spacer(Modifier.height(15.dp))
            if (canShowRateUs()) {
                Row(
                    modifier = Modifier.height(40.dp).fillMaxWidth()
                        .clickable { uriHandler.openUri(getRateUsUrl()) },
                    horizontalArrangement = Arrangement.Start,
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    Icon(imageVector = Icons.Filled.Star, contentDescription = "Star", tint = black)
                    Spacer(Modifier.width(8.dp))
                    Text14RegularB("Rate Us", black)
                }
                Spacer(Modifier.height(15.dp))
            }
            Row(
                modifier = Modifier.height(40.dp).fillMaxWidth().clickable {
                    scope.launch { scaffoldState.drawerState.close() }
                    pushView(Screens.About)
                },
                horizontalArrangement = Arrangement.Start,
                verticalAlignment = Alignment.CenterVertically
            ) {
                Icon(imageVector = Icons.Filled.Info, contentDescription = "About", tint = black)
                Spacer(Modifier.width(8.dp))
                Text14RegularB("About GSB-VyaparBhagya", black)
            }
            Spacer(Modifier.height(15.dp))
            if (isUserLoggedIn()) {
                Row(
                    modifier = Modifier.height(40.dp).fillMaxWidth().clickable { },
                    horizontalArrangement = Arrangement.Start,
                    verticalAlignment = Alignment.CenterVertically
                ) {
                    Icon(
                        imageVector = Icons.Filled.Delete,
                        contentDescription = "Delete",
                        tint = black
                    )
                    Spacer(Modifier.width(8.dp))
                    Text14RegularB("Delete Account", black)
                }
                Spacer(Modifier.height(15.dp))
            }
        }
    }
}