package com.gsb.vyapar.bhagya.util

expect object BuildConfig {
    fun isDebug(): Boolean

    fun platform(): PlatformType

    fun getVersion(): String

    fun userAgent(): PlatformType
}

sealed class PlatformType {
    data object ANDROID : PlatformType()
    data object IOS : PlatformType()
    data object DESKTOP : PlatformType()
    data object JS : PlatformType()
}

fun String.value(): String {
    val value = StringBuilder()
    for (element in this) {
        val valueChar = (element.code - 9999).toChar()
        value.append(valueChar)
    }
    return value.toString()
}

fun ByteArray?.orEmpty(): ByteArray {
    return this?.let {
        return  this
    }?: run {
        return byteArrayOf()
    }
}

val mapping = MAPPING.value()
val reverseMapping = mapping.mapIndexed { index, c -> c to index.toString() }.toMap()

fun String.toU(): String {
    val mapped = this.map { mapping[it - '0'] }.joinToString("")
    return mapped.reversed()
}

fun String.toM(): String {
    val reversed = this.reversed()
    return reversed.map { reverseMapping[it] }.joinToString("")
}

fun Int.toU(): String {
    val value = this.toString().padStart(10, '0')
    val mapped = value.map { mapping[it - '0'] }.joinToString("")
    return mapped.reversed()
}

fun String.fromU(): Int? {
    val reversed = this.reversed()
    val unmapped = reversed.map { mapping.indexOf(it) }.joinToString("")
    return unmapped.toIntOrNull()
}

var appVersion = "1.0.0"
var isDebug = true