package com.gsb.vyapar.bhagya.domain

import com.gsb.vyapar.bhagya.datasource.network.DtoId
import com.gsb.vyapar.bhagya.util.fromU
import io.ktor.util.decodeBase64Bytes
import io.ktor.util.encodeBase64
import korlibs.io.compression.compress
import korlibs.io.compression.deflate.GZIP
import korlibs.io.compression.uncompress
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.Serializable
import kotlinx.serialization.decodeFromByteArray
import kotlinx.serialization.encodeToByteArray
import kotlinx.serialization.protobuf.ProtoBuf

@Serializable
data class Business(
    val v: Int = 0,
    val i: Int = 0,
    val u: String = "",
    val d: D = D()
) {
    @Serializable
    data class D(
        val data: Map<String, String> = mapOf()
    )
}

fun DtoId.toBusiness(): Business {
    return Business(v= v, i = i, u = u, d = ProtoBuf.decodeFromByteArray<Business.D>(d.decodeBase64Bytes().uncompress(GZIP)))
}

fun Business.toDtoId(): DtoId {
    return DtoId(v = v, i = i, u = u, d = ProtoBuf.encodeToByteArray(d).compress(GZIP).encodeBase64())
}

fun String.decodeBusinessString(): Business {
    return try {
        ProtoBuf.decodeFromByteArray<Business>(this.decodeBase64Bytes().uncompress(GZIP))
    } catch (e: Exception) {
        Business()
    }
}

fun Business.encodeBusinessString(): String {
    return ProtoBuf.encodeToByteArray(this).compress(GZIP).encodeBase64()
}

