fun Throwable?.getMessage(): String {
    return this?.message?.let {
        return it
    }?: run {
        return "UnKnown Error"
    }
}

fun Int?.getString(): String {
    return this?.let { this.toString() }?: run { "" }
}

fun canShowRateUs(): Boolean {
    return BuildConfig.userAgent() != PlatformType.DESKTOP
}

fun getRateUsUrl(): String {
    return if (BuildConfig.userAgent() == PlatformType.ANDROID) {
        getState().getInfo().getAndroid()
    } else if (BuildConfig.userAgent() == PlatformType.IOS) {
        getState().getInfo().getIos()
    } else {
        getState().getInfo().getWebsite()
    }
}

fun getPrivacyUrl(): String {
    return getState().getInfo().getPrivacy()
