fun connectToServer() {
    showLoading()
    launchDefault {
        MQTTManager.connect()
        while(!MQTTManager.connected.value) {
            delay(1000)
        }
        hideLoading()
    }
}

fun requestOtpMobile(to: String, message: String, status: (Result<Unit>) -> Unit) {
    showLoading()
    launchDefault {
        var timoutCount = 0
        var isSuccess: Boolean
        var reason = ""
        while(!MQTTManager.connected.value && timoutCount <= 5) {
            delay(1000)
            timoutCount += 1
        }
        isSuccess = MQTTManager.connected.value
        if (isSuccess) {
            val requestId = Clock.System.now().epochSeconds.toString()
            MQTTManager.subscribe(requestId)
            delay(1000)
            MQTTManager.publish("PGsbVyaparBhagya", getMobileMessage(requestId, to, message).toMessageString())
            timoutCount = 0
            while (MQTTManager.messages.size <= 0 && timoutCount <= 5) {
                delay(1000)
                timoutCount += 1
            }
            val messages = MQTTManager.messages
            isSuccess = false
            messages.forEach {
                if (it.isSuccessMessage(requestId)) {
                    isSuccess = true
                } else if (it.isFailureMessage(requestId)) {
                    isSuccess = false
                    reason = it.failureReason(requestId).orEmpty()
                }
            }
            MQTTManager.unsubscribe(requestId)
        }
        hideLoading()
        if (!isSuccess && reason.isEmpty()) {
            reason = "Time Out, Please try again"
        }
        if (isSuccess) {
            showSuccess("OTP successfully sent to $to")
            status(Result.success(Unit))
        } else {
            showError(reason)
            status(Result.failure(Throwable(reason)))
        }
    }
}

