fun Int.formatSecondsToHHMM(): String {
    val minutes = this / 60
    val remainingSeconds = this % 60
    return minutes.toString().padStart(2, '0') + ":" + remainingSeconds.toString().padStart(2, '0')
}

fun launchLoginMobile() {
    getAuthUiState().resetAuthUiState(false)
    pushView(Screens.Login)
}

fun launchVerifyEmail() {
    getAuthUiState().resetAuthUiState(true)
    pushView(Screens.Login)
}

fun saveUserMobile(mobile: String, proceed: () -> Unit) {
    val oldUser = getState().getUser()
    val updatedUser = oldUser.d.user + (MOBILE to mobile) + (ID to mobile.toU()) + (CREATED to Clock.System.now().epochSeconds.toString()) + (UPDATED to Clock.System.now().epochSeconds.toString())
    saveUserToBackend(updatedUser) {
        saveUser(it)
        proceed()
    }
}

fun saveUserEmail(email: String, proceed: () -> Unit) {
    val oldUser = getState().getUser()
    val updatedUser = oldUser.d.user + (EMAIL to email) + (UPDATED to Clock.System.now().epochSeconds.toString())
    saveUserToBackend(updatedUser) {
        saveUser(it)
        proceed()
    }
}

fun saveUserUI(mobile: String, email: String, image: String, firstName: String, lastName: String, gotra: String, kuldev: String) {
    if (isMobileError(mobile) || mobile.isEmpty() ||
        isEmailError(email) || email.isEmpty() ||
        isWebsiteError(image) || image.isEmpty() ||
        isFirstNameError(firstName) || firstName.isEmpty() ||
        isLastNameError(lastName) || lastName.isEmpty() ||
        isGotraError(gotra) || gotra.isEmpty() ||
        isKuldevError(kuldev) || kuldev.isEmpty()) {
        if (isWebsiteError(image) || image.isEmpty()) {
            showError("Upload the profile picture")
        } else {
            showError("Fill all the details")
        }
    } else {
        val oldUser = getState().getUser()
        val updatedUser = oldUser.d.user + (MOBILE to mobile) + (EMAIL to email) + (IMAGE to image) + (FIRST_NAME to firstName) + (LAST_NAME to lastName) + (GOTRA to gotra) + (KULDEV to kuldev) + (UPDATED to Clock.System.now().epochSeconds.toString())
        saveUserToBackend(updatedUser) {
            saveUser(it)
            showSuccess("Saved")
            popView()
        }
    }
}

private fun saveUserToBackend(userMap: Map<String, String>, user: (User) -> Unit) {
    showLoading()
    launchDefault {
        var oldUser = getState().getUser()
        oldUser = oldUser.copy(v = oldUser.v + 1, d = User.D(userMap))
        val result = KtorClient.updateUser(oldUser)
        if (!result.isSuccess) {
            showError("Save User failed ${result.exceptionOrNull()?.getMessage()}")
        } else {
            user(oldUser)
        }
        hideLoading()
    }
}

fun saveUser(user: User) {
    updateState(getState().copy(user = user.encodeUserString()))
    Preference.user = getState().user
}

fun logout() {
    updateState(getState().copy(user = User().encodeUserString()))
    Preference.user = getState().user
}

fun isUserLoggedIn(): Boolean {
    return getState().getUser().getId().isNotEmpty()
}

fun isUserAdmin(): Boolean {
    return getState().getUser().getMobile() == ADMIN.value()
}

fun getUserIdUrl(userId: String): String {
    return "u/${userId}"
}

fun getOtherUser(userId: String, user: (User?) -> Unit) {
    val userString = Preference.getCacheData(getUserIdUrl(userId))
    if (userString.isNullOrEmpty()) {
        launchDefault {
            val result = KtorClient.getUser(userId)
            if (result.isSuccess) {
                val userResult = result.getOrNull()
                if (userResult != null) {
                    val finalUser = userResult.toUser()
                    Preference.setCacheData(getUserIdUrl(userId), finalUser.encodeUserString())
                    user(finalUser)
                } else {
                    user(null)
                }
            } else {
                user(null)
            }
        }
    } else {
        return try {
            user(userString.decodeUserString())
        } catch (e: Exception) {
            user(null)
        }
    }
}