package com.gsb.vyapar.bhagya.interactor.util

import androidx.compose.runtime.snapshots.SnapshotStateMap
import com.gsb.vyapar.bhagya.datasource.cache.SqlDelightClient
import com.gsb.vyapar.bhagya.datasource.network.KtorClient
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.getState
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.launchDefault
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.updateState

fun checkImageCache(url: String) {
    launchDefault {
        var image = getState().imageMap[url]
        if (image == null) {
            image = SqlDelightClient.getImage(url)
            if (image == null) {
                image = KtorClient.downloadImage(url).getOrNull()
                if (image != null) {
                    SqlDelightClient.putImage(url, image)
                    updateState(getState().copy(imageMap = getNewImageMap(url, image)))
                }
            } else {
                updateState(getState().copy(imageMap = getNewImageMap(url, image)))
            }
        }
    }
}

fun getNewImageMap(url: String, image: ByteArray): SnapshotStateMap<String, ByteArray> {
    val map = getState().imageMap
    map[url] = image
    return map
}

fun uploadImage(data: ByteArray, fileName: String, imageUrl: (String) -> Unit) {
    if (isImageLessThan1MB(data)) {
        showLoading()
        launchDefault {
            val result = KtorClient.uploadImage(data, sanitizeFilename(fileName))
            hideLoading()
            if (result.isSuccess) {
                val imageUploadResponse = result.getOrNull()
                imageUploadResponse?.data?.url?.let {
                    imageUrl(it)
                    showSuccess("Image Uploaded")
                }?: run {
                    showError("There was a problem while uploading image")
                }
            } else {
                showError("There was a problem while uploading image")
            }
        }
    } else {
        showError("Image size should be less than 1 MB")
    }
}

fun isImageLessThan1MB(imageData: ByteArray): Boolean {
    val maxSizeInBytes = 1 * 1024 * 1024 // 1 MB in bytes
    return imageData.size < maxSizeInBytes
}

fun sanitizeFilename(filename: String): String {
    val lastDotIndex = filename.lastIndexOf('.')

    return if (lastDotIndex != -1) {
        val namePart = filename.substring(0, lastDotIndex).replace("[^a-zA-Z0-9]".toRegex(), "")
        val extension = filename.substring(lastDotIndex)
        "$namePart$extension"
    } else {
        filename.replace("[^a-zA-Z0-9]".toRegex(), "")
    }
}