@Composable
fun ManageBusinessView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Scaffold(
            floatingActionButton = {
                FloatingActionButton(backgroundColor = primary, contentColor = white, onClick = {
                    launchAddEditMBusiness(Business())
                }) { Icon(imageVector = Icons.Default.Add, contentDescription = "add") }
            },
            modifier = Modifier.fillMaxSize()
        ) { paddingValues ->
            Column(
                modifier = Modifier.fillMaxWidth().padding(paddingValues)
            ) {
                Toolbar("Manage Business")
                TabView(uiState, onTriggerEvent)
            }
        }

    }
}

@OptIn(ExperimentalMaterialApi::class)
@Composable
fun TabView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val tabs = listOf(
        TabItem("In Review"),
        TabItem("Approved"),
    )
    var selectedTabIndex by remember { mutableStateOf(0) }
    Column {
        TabRow(
            selectedTabIndex = selectedTabIndex,
            backgroundColor = primary,
            contentColor = white,
            indicator = { tabPositions ->
                TabRowDefaults.Indicator(
                    color = white,
                    modifier = Modifier
                        .tabIndicatorOffset(tabPositions[selectedTabIndex])
                        .fillMaxWidth()
                )
            },
            modifier = Modifier
                .shadow(AppBarDefaults.TopAppBarElevation)
                .zIndex(1f)
        ) {
            tabs.forEachIndexed { index, tab ->
                Tab(
                    selected = selectedTabIndex == index,
                    onClick = { selectedTabIndex = index },
                    modifier = Modifier.padding(8.dp),
                    content = {
                        Row(horizontalArrangement = Arrangement.Center, verticalAlignment = Alignment.CenterVertically) {
                            Text16Medium(
                                text = tab.title,
                                modifier = Modifier.padding(8.dp),
                                color = if (selectedTabIndex == index) white else offwhite
                            )
                        }
                    }
                )
            }
        }
        when (selectedTabIndex) {
            0 -> {
                val pullState = rememberPullRefreshState(
                    refreshing = uiState.businessUiState.isRefreshing,
                    onRefresh = {
                        getMBusinessUserUI()
                    },
                )
                LazyColumn(modifier = Modifier.pullRefresh(pullState, enabled = true).fillMaxWidth().padding(8.dp)) {
                    items(uiState.businessUiState.mBusinessList) { business ->
                        BusinessCard(uiState, onTriggerEvent, business) {
                            launchAddEditMBusiness(business)
                        }
                    }
                }
            }
            1 -> {
                Column(
                    modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth()
                        .padding(16.dp),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {

                }
            }
        }
    }
    LaunchedEffect(Unit) {
        getMBusinessUserUI()
    }
}

data class TabItem(val title:String)