package com.gsb.vyapar.bhagya.presentation.splash.manager

import com.gsb.vyapar.bhagya.datasource.mqtt.MQTTManager
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.launchDefault
import kotlinx.coroutines.delay
import com.gsb.vyapar.bhagya.presentation.splash.event.SplashUiEvents
import com.gsb.vyapar.bhagya.presentation.splash.event.SplashUiEvents.*
import com.gsb.vyapar.bhagya.presentation.splash.util.*

object SplashManager {
    fun onTriggerEvent(event: SplashUiEvents) {
        when (event) {
            GetSplashUiData -> {
                launchDefault {
                    val businessNumber = getLastBusinessNumber()
                    checkToken()
                    getConfig()
                    getCategories()
                    getSubCategories()
                    getInfo()
                    delay(3000)
                    goToNavigation()
                    MQTTManager.createMqttClient()
                }
            }
        }
    }
}