import io.github.vinceglb.filekit.core.PickerType
import kotlinx.coroutines.launch

@Composable
fun RegisterView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    val uriHandler = LocalUriHandler.current
    var mobile by remember { mutableStateOf("") }
    var email by remember { mutableStateOf("") }
    var image by remember { mutableStateOf("") }
    var firstName by remember { mutableStateOf("") }
    var lastName by remember { mutableStateOf("") }
    var gotra by remember { mutableStateOf("") }
    var kuldev by remember { mutableStateOf("") }
    var isRegister by remember { mutableStateOf(firstName.isEmpty()) }
    val coroutineScope = rememberCoroutineScope()
    val imagePickerLauncher = rememberFilePickerLauncher(PickerType.Image) { selectedImage ->
        if (selectedImage != null) coroutineScope.launch { uploadImage(selectedImage.readBytes().orEmpty(), selectedImage.name) { image = it } }
    }
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Column(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar(if (isRegister) "Register" else "Edit Profile")
            Column(
                modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth().padding(16.dp),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Spacer(Modifier.height(15.dp))
                uiState.getImage(image.ifEmpty { BLANK_PROFILE.value() })?.let { imageBitmap ->
                    CommonImage(imageBitmap, 150.dp, 150.dp, Modifier.clip(CircleShape).clickable { imagePickerLauncher.launch() })
                }?: PlaceHolder(150.dp, 150.dp)
                Spacer(Modifier.height(15.dp))
                FirstNameEditText(firstName) { firstName = it }
                Spacer(Modifier.height(8.dp))
                LastNameEditText(lastName) { lastName = it }
                Spacer(Modifier.height(8.dp))
                MobileNumberEditText(mobile, false) {  }
                Spacer(Modifier.height(8.dp))
                EmailEditText(email, false) {  }
                Spacer(Modifier.height(8.dp))
                GotraEditText(gotra) { gotra = it }
                Spacer(Modifier.height(8.dp))
                KuldevEditText(kuldev) { kuldev = it }
                Spacer(Modifier.height(16.dp))
                GVBButton(text = if (isRegister) "Register" else "Save") {
                    saveUserUI(mobile, email, image, firstName, lastName, gotra, kuldev)
                }
                Spacer(Modifier.weight(1f))
                Row(modifier = Modifier.fillMaxWidth().padding(top = 16.dp), horizontalArrangement = Arrangement.SpaceBetween) {
                    Text14RegularB("Privacy Policy", primary, Modifier.clickable { uriHandler.openUri(getPrivacyUrl()) })
                    Text14RegularB("Terms & Conditions", primary, Modifier.clickable { uriHandler.openUri(getTermsUrl()) })
                }
            }
        }
    }
    LaunchedEffect(Unit) {
        val user = uiState.getUser()
        mobile = user.getMobile()
        email = user.getEmail()
        image = user.getImage()
        firstName = user.getFirstName()
        lastName = user.getLastName()
        gotra = user.getGotra()
        kuldev = user.getKuldev()
        isRegister = firstName.isEmpty()
    }
}